  display: flex;
  align-self: flex-start;
  gap: 0.3rem;
  margin-left: 0.8rem;
  padding-left: 0.4rem;
  border: 1px solid transparent;

  &.display-none {
    display: none;
  }

  &:hover {
    border-radius: 0.2rem;
    border: 1px solid #E4E7EA;

    & .todo-priority-remove {
      visibility: visible;
    }
  }

  & .todo-priority-icon {
    width: 1.25rem;
  }

  & .todo-priority-title {
    font-size: 0.9rem;
    font-weight: 600;
  }

  & .todo-priority-remove {
    align-self: stretch;
    visibility: hidden;
    padding-left: 0.2rem;
    padding-right: 0.2rem;
    border-radius: 0.2rem;

    &:hover {
      background-color: #E7E9EC;
    }

    & .todo-priority-remove-icon {
      width: 1rem;
    }
  }
}`, "",{"version":3,"sources":["webpack://./src/components/body/content/project/todo/todo-priority/todo-priority.css"],"names":[],"mappings":"AAAA;EACE,aAAa;EACb,sBAAsB;EACtB,WAAW;EACX,mBAAmB;EACnB,oBAAoB;EACpB,6BAA6B;;EAE7B;IACE,aAAa;EACf;;EAEA;IACE,qBAAqB;IACrB,yBAAyB;;IAEzB;MACE,mBAAmB;IACrB;EACF;;EAEA;IACE,cAAc;EAChB;;EAEA;IACE,iBAAiB;IACjB,gBAAgB;EAClB;;EAEA;IACE,mBAAmB;IACnB,kBAAkB;IAClB,oBAAoB;IACpB,qBAAqB;IACrB,qBAAqB;;IAErB;MACE,yBAAyB;IAC3B;;IAEA;MACE,WAAW;IACb;EACF;AACF","sourcesContent":[".todo-priority {\n  display: flex;\n  align-self: flex-start;\n  gap: 0.3rem;\n  margin-left: 0.8rem;\n  padding-left: 0.4rem;\n  border: 1px solid transparent;\n\n  &.display-none {\n    display: none;\n  }\n\n  &:hover {\n    border-radius: 0.2rem;\n    border: 1px solid #E4E7EA;\n\n    & .todo-priority-remove {\n      visibility: visible;\n    }\n  }\n\n  & .todo-priority-icon {\n    width: 1.25rem;\n  }\n\n  & .todo-priority-title {\n    font-size: 0.9rem;\n    font-weight: 600;\n  }\n\n  & .todo-priority-remove {\n    align-self: stretch;\n    visibility: hidden;\n    padding-left: 0.2rem;\n    padding-right: 0.2rem;\n    border-radius: 0.2rem;\n\n    &:hover {\n      background-color: #E7E9EC;\n    }\n\n    & .todo-priority-remove-icon {\n      width: 1rem;\n    }\n  }\n}"],"sourceRoot":""}]);
