___CSS_LOADER_EXPORT___.push([module.id, `.todo-move-action {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  width: 1.6rem;
  aspect-ratio: 1;
  border: 1px solid transparent;

  &.display-none {
    display: none;
  }

  &:hover {
    border-radius: 0.2rem;
    border: 1px solid #E4E7EA;
  }

  & .todo-move-action-icon {
    width: 1.25rem;
  }

  & .todo-move-action-dropdown {
    display: flex;
    flex-direction: column;
    gap: 0.1rem;
    position: absolute;
    top: 1.8rem;
    width: max-content;
    padding: 0.5rem;
    border-radius: 0.5rem;
    background-color: #2D3238;

    & .dropdown-project-label {
      display: flex;
      gap: 0.3rem;
      padding-left: 0.3rem;
      padding-right: 0.5rem;
      border-radius: 0.2rem;

      &:hover {
        background-color: #6A98F8;
      }

      & .dropdown-project-label-icon {
        width: 1.25rem;
      }
    
      & .dropdown-project-label-title {
        font-size: 0.9rem;
        color: #ffffff;
        font-weight: 600;
      }
    }

    &.display-none {
      display: none;
    }
  }
}`, "",{"version":3,"sources":["webpack://./src/components/body/content/project/todo/todo-move-action/todo-move-action.css"],"names":[],"mappings":"AAAA;EACE,aAAa;EACb,uBAAuB;EACvB,mBAAmB;EACnB,kBAAkB;EAClB,aAAa;EACb,eAAe;EACf,6BAA6B;;EAE7B;IACE,aAAa;EACf;;EAEA;IACE,qBAAqB;IACrB,yBAAyB;EAC3B;;EAEA;IACE,cAAc;EAChB;;EAEA;IACE,aAAa;IACb,sBAAsB;IACtB,WAAW;IACX,kBAAkB;IAClB,WAAW;IACX,kBAAkB;IAClB,eAAe;IACf,qBAAqB;IACrB,yBAAyB;;IAEzB;MACE,aAAa;MACb,WAAW;MACX,oBAAoB;MACpB,qBAAqB;MACrB,qBAAqB;;MAErB;QACE,yBAAyB;MAC3B;;MAEA;QACE,cAAc;MAChB;;MAEA;QACE,iBAAiB;QACjB,cAAc;QACd,gBAAgB;MAClB;IACF;;IAEA;MACE,aAAa;IACf;EACF;AACF","sourcesContent":[".todo-move-action {\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  position: relative;\n  width: 1.6rem;\n  aspect-ratio: 1;\n  border: 1px solid transparent;\n\n  &.display-none {\n    display: none;\n  }\n\n  &:hover {\n    border-radius: 0.2rem;\n    border: 1px solid #E4E7EA;\n  }\n\n  & .todo-move-action-icon {\n    width: 1.25rem;\n  }\n\n  & .todo-move-action-dropdown {\n    display: flex;\n    flex-direction: column;\n    gap: 0.1rem;\n    position: absolute;\n    top: 1.8rem;\n    width: max-content;\n    padding: 0.5rem;\n    border-radius: 0.5rem;\n    background-color: #2D3238;\n\n    & .dropdown-project-label {\n      display: flex;\n      gap: 0.3rem;\n      padding-left: 0.3rem;\n      padding-right: 0.5rem;\n      border-radius: 0.2rem;\n\n      &:hover {\n        background-color: #6A98F8;\n      }\n\n      & .dropdown-project-label-icon {\n        width: 1.25rem;\n      }\n    \n      & .dropdown-project-label-title {\n        font-size: 0.9rem;\n        color: #ffffff;\n        font-weight: 600;\n      }\n    }\n\n    &.display-none {\n      display: none;\n    }\n  }\n}"],"sourceRoot":""}]);
