___CSS_LOADER_EXPORT___.push([module.id, `.project-label {
  display: flex;
  align-items: center;
  gap: 0.3rem;
  height: 1.6rem;
  padding-right: 0.7rem;
  padding-left: 0.4rem;
  border-radius: 0.3rem;

  &.selected {
    background-color: #E1E4E8;
  }

  &.margin-bottom {
    margin-bottom: 0.7rem;
  }

  & .project-label-icon {
    width: 1.2rem;
  }

  & .project-label-title {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    font-size: 0.9rem;
    cursor: default;

    &.bold {
      font-weight: 500;
    }
  }
}
