___CSS_LOADER_EXPORT___.push([module.id, `.project-title {
  display: flex;
  gap: 0.7rem;
  align-items: center;
  padding-left: 0.2rem;

  & .project-title-done {
    appearance: none;
    width: 1.2rem;
    aspect-ratio: 1;
    border-radius: 50%;
    outline: 0.2rem solid #0162c1;
    border: 0.15rem solid transparent;

    &:checked {
      background-color: #0162c1;
      border-color: white;
    }
  }

  & .project-title-icon {
    width: 1.9rem;
  }

  & .project-title-title {
    flex-grow: 1;
    font-size: 1.5rem;
    font-weight: 600;
    caret-color: #0162c1;

    &:focus {
      outline: none;
    }

    &:empty::before {
      content: attr(data-placeholder);
      color: #b0b2b8;
    }
  }
}
